

html, body, .root {
  height: 100%;
  font-family: 'Times New Roman', Times, serif;
}

.app { background: white; }

a {
  text-decoration: none;
  cursor: pointer;
}

/* main page */
.list-books-title, .list-books-title-search {
  background: #F74C9A;
  text-align: center;
}

.list-books-title-search { padding: 10px; }

.list-books-title-search h1 { color: #fff }

.list-books-title img { position: relative; }

@media (max-width: 600px) {
  .list-books-title img {
    width: 50%;
  }
}

.list-books-content {
  flex: 1;
  background: #F74C9A;
  text-align: center;
}

.list-books-content h2 {
  color: #fff;
  font-weight: inherit;
}

.list-books-content button {
  background-color: #fff;
  color: #F74C9A;
  padding: 10px;
  border: 1px solid;
  font-weight: bold;
  font-size: 1rem;
  margin: 0 auto 50px auto;
}

.bookshelf{ background-color: #F74C9A; }

.bookshelf-title {
  text-align: left;
  padding: 10px;
  font-size: 2rem;
  color: #fff;
}

.bookshelf-books { text-align: center; }

.link-new-books { text-decoration: none; }

.new-books{ background: #fff; }

.new-books h1{
  font-size: 2rem;
  text-align: center;
  text-decoration: none;
}

.new-books img {
  font-size: inherit;
  line-height: 19px;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: #64DD17;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 28px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

footer, footer a{
  color: #F74C9A;
  padding: 10px;
  font-weight: bold;
  text-align: center;
}

/* search page */
.message-error {
  justify-content: center;
  background-color: #fff;
  margin: 10px;
  padding: 50px;
}

.message-error p{
  color: #000;
  font-weight: inherit;
  line-height: 1.5;
}

.search-books-bar {
  width: 100%;
  top: 0;
  left: 0;
  display: flex;
  margin-bottom: 20px;
}

.search-books-input-wrapper {
  flex: 1;
  background: #e9e;
}

.search-books-bar input {
  width: 100%;
  padding: 15px 10px;
  font-size: 1.25em;
  border: none;
  outline: none;
}

.close-search {
  display: block;
  top: 20px;
  left: 15px;
  width: 50px;
  height: 53px;
  background: white;
  background-image: url('./icons/arrow-back.svg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: 28px;
  font-size: 0;
}

.search-books-results { padding: 80px 10px 20px; }

/* books grid */

.books-grid {
  list-style-type: none;
  padding: 0 0 50px 0;
  margin: 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.books-grid li {
  padding: 10px 15px;
  text-align: left;
}

.book { width: 140px; }

.book-title,
.book-authors,
.book-authors-want { font-size: 0.8em; }

.book-title { margin-top: 10px;}

.book-authors { color: #fff; }

.book-top {
  position: relative;
  height: 200px;
  display: flex;
  align-items: flex-end;
}

.book-shelf-changer {
  position: absolute;
  right: 0;
  bottom: -10px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #64DD17;
  background-image: url('./images/change.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 20px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.book-shelf-changer select {
  width: 100%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}

/* book cover */

.book-cover {
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
  background: #eee;
}
.book-cover-title {
  padding: 20px 10px 0;
  text-align: center;
  font-size: 0.8em;
}
